<template>
  <div class="container">
    <h1>Latest Post</h1>
    <!-- CREATE POST HERE -->
    <div class="create-post">
      <label for="create-post">
...Say Something
      </label>
      <input type="text" id="create-post" v-model="text" placeholder="create post">
      <button v-on:click="createPost">Post!</button>
    </div>
    <hr>
    <p class="error" v-if=error>{{ error }}</p>
    <div class="posts-container">
      <div class="posts" v-for="(post, index) in posts" v-bind:item="post" v-bind:index="index"
      v-bind:key="post._id" v-on:dblclick="deletePost(post._id)">
        {{ `${post.createdAt.getDate()}/${post.createdAt.getMonth()}/${post.createdAt.getFullYear()} 
        ` }}
        <p class="text">{{ post.text }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import PostService from '../PostService'
export default {
  name: 'PostComponent',
  data () {
    return {
      posts: [],
      error: '',
      text: ''
    }
  },
  async created () {
    try {
      this.posts = await PostService.getPosts()
    } catch (err) {
      this.error = err.message
    }
  },
  methods: {
    async createPost () {
      await PostService.insertPost(this.text)
 this.posts = await PostService.getPosts()
    },
    async deletePost (id) {
      await PostService.deletePost(id)
 this.posts = await PostService.getPosts()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
div.container{
  max-width: 800px;
  margin: 0 auto;
}
p.error{
  border: 1 px solid #ff5b5f;
  background-color: #ffc5c1;
  margin-bottom: 15px;
  padding: 10px;
}
.posts{
  position: relative;
  background:#bcffba;
  border-bottom:1px solid #5bd658;
  padding: 10px 10px 30px 10px;
  margin-bottom: 15px;
}
div.created-at{
  position: absolute;
  top: 0;
  left: 0;
  padding: 5px 15px;
  background-color: darkgreen;
  color: white;
  font-size: 13px;
}
p.text{
  margin-bottom: 0;
  font-weight: 700;
  font-size: 22px;
}
</style>



